* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
}

.App {
  min-height: 100vh;
  position: relative;
  background-color: #313131;
}

.camera {
  position: relative;
}

.result{
  position: fixed;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.9);
  transition: 0.4s;
}

.result.hasPhoto {
  left: 0;
}

video {
  width: 100%;
  max-width: 100%;
  height: auto;
}

button {
  position: absolute;
  bottom: 20px;
  left: 20px;

  appearance: none;
  border: none;
  outline: none;

  padding: 8px 16px;
  background-image: linear-gradient(to right, #a8cc97, #6ba45a);
  color: #FFF;
  font-size: 24px;
  font-weight:700;
  cursor: pointer;
}

button:hover{
  background-image: none;
  background-color: #f4c3ed;
  color: #FFF;
}

canvas {
  width:100%;
  height: auto;
}